import React from 'react'
import { useSelector, useDispatch } from 'react-redux'

import {
  selectDebugMode
} from '../../features/display/displaySlice'
import {
  setCurrentAction
} from '../../features/json/jsonSlice'

function ButtonAddSignature(props) {

  const json = props.controlJson
  const { Type, Title, Id, IsVisible = true } = json
  // console.log(`--->: Text -> IsVisible`, IsVisible)

  const dispatch = useDispatch()

  const debugMode = useSelector(selectDebugMode)

  function clickAddSignatureHandler() {
    dispatch(setCurrentAction('add-signature'))
  }

  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
        </div>

        <div className="control">
          <button className="btn btn-secondary" onClick={clickAddSignatureHandler}>
            {Title}
          </button>
        </div>

      </div>
      :
      ''
  )
}

export default ButtonAddSignature
