import { createSlice } from '@reduxjs/toolkit'

export const jsonSlice = createSlice({
  name: 'json',
  initialState: {
    // value: {},
    // documentStatus: 'init',

    // // Persistence
    value: JSON.parse(localStorage.getItem('json')) || {},
    documentStatus: 'loaded',
    currentAction: '',
    currentUser: '',
    currentContact: { name: '', phoneNumber: '', companyName: '' },
    repeaterValue: []
  },
  reducers: {
    setJson: (state, action) => {
      console.log(`setJson : `, action.payload)

      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.value = action.payload

      // Initial JSON to be set if json is not present (new document)
      // localStorage.se  tItem('initialJson', JSON.stringify(normalizeJson(JSON.parse(configJson))))
      localStorage.setItem('json', JSON.stringify(action.payload))
    },
    setDocumentStatus: (state, action) => {
      // console.log(`setDocumentStatut : `, action.payload)
      state.documentStatus = action.payload
    },
    setCurrentAction: (state, action) => {
      state.currentAction = action.payload
    },
    setCurrentUser: (state, action) => {
      state.currentUser = action.payload
    },
    setCurrentContact: (state, action) => {
      state.currentContact = action.payload
    },
    setRepeaterValue: (state, action) => {
      state.repeaterValue = action.payload
    }
  },
})

export const { setJson, setDocumentStatus, setCurrentAction, setCurrentUser, setCurrentContact, setRepeaterValue } = jsonSlice.actions

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state) => state.counter.value)`
export const selectJson = state => state.json.value
export const selectDocumentStatus = state => state.json.documentStatus
export const selectCurrentAction = state => state.json.currentAction
export const selectCurrentUser = state => state.json.currentUser
export const selectCurrentContact = state => state.json.currentContact
export const selectRepeaterValue = state => state.json.repeaterValue

export default jsonSlice.reducer