import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'

import {
  selectDebugMode
} from '../../features/display/displaySlice'

import Figure from '../Figure'

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faImage } from '@fortawesome/free-solid-svg-icons'

function Image(props) {

  const json = props.controlJson
  const { Type, Title, Id, Data, IsVisible = true, Images = [] } = json
  // console.log(`--->: Image -> IsVisible`, IsVisible)


  const debugMode = useSelector(selectDebugMode)

  const [selectedFile, setSelectedFile] = useState('')


  function onChangeHandler(pEvent) {
    // setValue(pEvent.target.value)
    // const newJson = JSON.parse(JSON.stringify(json))
    // newJson.Value = pEvent.target.value
    console.log('pEvent: ', pEvent)
    // props.updateJson(newJson)
  }

  function onCloseHandler(pEvent) {
    console.log('---> ', pEvent)

  }

  useEffect(() => {
    // console.log('new data in control')
  }, [json])

  const images = Images.map((image, index) => (
    <div className="control-row" key={`image${index}`}>
      <Figure source={`data:image/png; base64, ${image}`} onClose={onCloseHandler} />
    </div>
  ))

  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
        </div>
        <div className="control">
          <div className="control-row">
            <h3 htmlFor="title">{Title}</h3>
            <label htmlFor="file" className="btn btn-primary btn-label">
              <FontAwesomeIcon icon={faImage} />
              Select an image
            </label>
            <input type="file" id="file" name="file" className="btn-file" onChange={onChangeHandler} />
          </div>
          {images}
        </div>
      </div>
      :
      ''
  )
}

export default Image
