import React, { useState } from 'react'
import { useSelector } from 'react-redux'

import {
  selectDebugMode
} from '../../features/display/displaySlice'


import QrReader from 'react-qr-reader'
import { format } from 'date-fns'



function QRScanner(props) {

  const json = props.controlJson
  const { Type, Title, Id, IsVisible = true, Value, DateTimeValue } = json

  const [isScanning, setIsScanning] = useState(false)
  const debugMode = useSelector(selectDebugMode)

  const handleScan = pData => {
    if (pData) {
      updateValues(pData, format(new Date(), 'yyyy-MM-dd hh:mm a'))
      setIsScanning(false)
    }
  }

  const handleError = pErr => {
    console.error(pErr)
  }

  function deleteValue() {
    updateValues('', '')
  }

  function updateValues(pScanValue, pTimestamp) {
    const newJson = JSON.parse(JSON.stringify(json))
    newJson.Value = pScanValue
    newJson.DateTimeValue = pTimestamp
    // console.log('value: ', json)
    props.updateJson(newJson)
  }

  const qrReader = (isScanning) ? <QrReader
    delay={300}
    onError={handleError}
    onScan={handleScan}
    style={{ width: '100%' }}
  /> : ''

  const btScan = (!isScanning) ? <button className="btn btn-primary" onClick={() => setIsScanning(!isScanning)}>Scan a code</button>
    :
    ''


  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
        </div>
        <div className="control">
          <h3>{Title}</h3>
          <div className="control-row">
            {btScan}
            {qrReader}
          </div>
          <div className="control-row">
            <div className="qrscan-record qrscan-record-3cols">
              <div>{Value}</div>
              <div>{DateTimeValue}</div>
              <button className="btn btn-secondary btn-small" onClick={deleteValue}>delete</button>
            </div>
          </div>
        </div>

      </div>
      :
      ''
  )

}

export default QRScanner
