import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import Modal from '../Modal'

import {
  selectDebugMode
} from '../../features/display/displaySlice'
import {
  setCurrentAction,
  selectCurrentAction
} from '../../features/json/jsonSlice'


import { format } from 'date-fns'

function RegisterSignatures(props) {

  const json = props.controlJson
  const { Type, Title, Id, IsVisible = true, Value } = json

  const debugMode = useSelector(selectDebugMode)
  const currentAction = useSelector(selectCurrentAction)
  const dispatch = useDispatch()

  const [isModalVisible, setIsModalVisible] = useState(false)
  const [modalOptions, setModalOptions] = useState({ message: '' })

  useEffect(() => {
    // console.log('---> currentAction: ', currentAction)
    if (currentAction === 'add-signature') {
      // CHECK IF SIGNATURE IS EMPTY
      const registerValues = JSON.parse(Value)
      registerValues.push({
        Id: registerValues.length + 1,
        Name: document.getElementById('SignatureSignOnOffName').value,
        SignOnDateTime: format(new Date(), 'yyyy-MM-dd hh:mm:ss'),
        Signature: document.getElementById('SignatureSignOnOffSignature').src.replace("data:image/png;base64,", ""),
      })
      const newJson = JSON.parse(JSON.stringify(json))
      newJson.Value = JSON.stringify(registerValues)
      props.updateJson(newJson)

      // Need to be cleared in the JSON
      // document.getElementById('SignatureSignOnOffName').value = ''
      document.getElementById('signatureText').value = ''
      document.getElementById('SignatureSignOnOffSignature').src = ''
      dispatch(setCurrentAction(''))
    }

  }, [currentAction])

  function signOffClickHandler(pId) {
    setModalOptions({
      message: 'Are you sure you want to sign off?', bgColor: '#000000', callback: () => {
        const registerValues = JSON.parse(Value)
        const signatureIndex = registerValues.findIndex(element => element.Id === pId)

        registerValues[signatureIndex].SignOffDateTime = format(new Date(), 'yyyy-MM-dd hh:mm:ss')
        // console.log('---> SIGNING OFF: ', registerValues)

        const newJson = JSON.parse(JSON.stringify(json))
        // console.log(`--->: signOffClickHandler -> newJson`, newJson)
        newJson.Value = JSON.stringify(registerValues)
        props.updateJson(newJson)

        setIsModalVisible(false)
      }
    })
    setIsModalVisible(true)



  }

  const registerList = JSON.parse(Value).reverse().map((item, index) => {
    return (
      <div key={index} className="register-signature-row">
        <img src={`data:image/png;base64,${item.Signature}`} alt="" width="250px" />
        <div>
          <p>{item.Name}</p>
          <p>Sign On: {item.SignOnDateTime}</p>
          <p>Sign Off: {item.SignOffDateTime || <button className="btn btn-primary btn-small" onClick={() => signOffClickHandler(item.Id)}>Sign</button>}</p>
        </div>
      </div>
    )
  })

  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
        </div>
        <div className="control">
          {registerList}
        </div>
        <Modal
          visibility={isModalVisible}
          toggleVisibility={() => setIsModalVisible(!isModalVisible)}
          message={modalOptions.message}
          css={{ backgroundColor: '#000000' }}
          backdrop={'rgba(0,0,0,0.3)'}
          callback={modalOptions.callback}
        />
      </div>
      :
      ''
  )
}

export default RegisterSignatures