import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'

import {
  selectDebugMode
} from '../../features/display/displaySlice'

import Modal from '../Modal'

import QrReader from 'react-qr-reader'
import { format } from 'date-fns'


function QRScannerList(props) {

  const json = props.controlJson
  const { Type, Title, Id, IsVisible = true, Value, DateTimeValue } = json

  const [isScanning, setIsScanning] = useState(false)
  const [scanOffIndex, setScanOffIndex] = useState(-1)

  const debugMode = useSelector(selectDebugMode)
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [modalOptions, setModalOptions] = useState({ message: '' })

  const handleScan = pData => {
    if (pData) {

      const newList = JSON.parse(Value)

      if (scanOffIndex < 0) {
        newList.push({ Id: newList.length, Name: pData, ScanOnDateTime: format(new Date(), 'yyyy-MM-dd hh:mm a') })
      }
      else {
        if (pData === newList[scanOffIndex].Name) {
          newList[scanOffIndex].ScanOffDateTime = format(new Date(), 'yyyy-MM-dd hh:mm a')
          // console.log('---> Add scan off, scanOffIndex on: ', newList[scanOffIndex])
        }
        else {
          setModalOptions({ message: 'Please rescan the same QR code to sign off', bgColor: '#000000', callback: null })
          setIsModalVisible(true)
        }
        setScanOffIndex(-1)
      }

      updateValues(newList)
      setIsScanning(false)

    }
  }

  useEffect(() => {
    console.log(`--->: QRScannerList -> scanOffIndex`, scanOffIndex)
    if (scanOffIndex >= 0) setIsScanning(true)
  }, [scanOffIndex])

  const handleError = pErr => {
    console.error(pErr)
  }

  function deleteValue(pIndex) {
    setModalOptions({
      message: 'Are you sure you want to delete?', bgColor: '#000000', callback: () => {
        const newList = JSON.parse(Value).filter((value, index) => index !== pIndex)
        updateValues(newList)
        setIsModalVisible(false)
      }
    })
    setIsModalVisible(true)

  }

  function updateValues(pValue) {
    // console.log('value: ', pValue)
    const newJson = JSON.parse(JSON.stringify(json))
    newJson.Value = JSON.stringify(pValue)
    // newJson.DateTimeValue = pTimestamp
    props.updateJson(newJson)
  }

  const qrReader = (isScanning) ? <QrReader
    delay={300}
    onError={handleError}
    onScan={handleScan}
    style={{ width: '100%' }}
  /> : ''

  const btScan = (!isScanning) ? <button className="btn btn-primary" onClick={() => setIsScanning(!isScanning)}>Add to QR Scanner list</button>
    :
    ''

  const scanList = JSON.parse(Value).map((value, index) => {
    return (
      <div key={`scan${index}`} className="qrscan-record">
        <div>{value.Name}</div>
        <div>{value.ScanOnDateTime || <button className="btn btn-primary btn-small" onClick={() => setScanOffIndex(index)}>Scan Off</button>}</div>
        <div>{value.ScanOffDateTime || <button className="btn btn-primary btn-small" onClick={() => setScanOffIndex(index)}>Scan Off</button>}</div>
        <button className="btn btn-secondary btn-small" onClick={() => deleteValue(index)}>delete</button>
      </div>
    )
  })

  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
        </div>
        <div className="control">
          <h3>{Title}</h3>
          <div className="control-row">
            {btScan}
            {qrReader}
          </div>
          <div className="control-row">
            <div className="qrscan-record">
              <div>Name</div>
              <div>Scan On</div>
              <div>Scan Off</div>
              <span></span>
            </div>
            {scanList}
          </div>
        </div>
        <Modal
          visibility={isModalVisible}
          toggleVisibility={() => setIsModalVisible(!isModalVisible)}
          message={modalOptions.message}
          css={{ backgroundColor: '#000000' }}
          backdrop={'rgba(0,0,0,0.3)'}
          callback={modalOptions.callback}
        />
      </div >
      :
      ''
  )

}

export default QRScannerList
